.containersx {
	width: 100%;
	max-width: 1200px;
	margin: 0 auto;
	padding: 50px 0;
	text-align: center;
  }
  
  .header {
	font-size: 2.5rem;
	color: #0077c2;
	margin-bottom: 40px;
  }
  
  .content {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
  }
  
  .box {
	width: calc(50% - 20px);
	margin-bottom: 40px;
	padding: 20px;
	border: 1px solid #ccc;
	border-radius: 5px;
	background-color: #f5f5f5;
	box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
  }
  
  .box img {
	display: block;
	width: 60px;
	margin: 0 auto 20px;
  }
  
  .box h3 {
	font-size: 1.2rem;
	color: #0077c2;
	margin-bottom: 10px;
  }
  
  .box p {
	font-size: 1rem;
	color: #333;
	line-height: 1
  }
  @media (max-width: 600px) {
    .content{
        display: block;
    }
    .box{
        width: auto;
    }
  }

/* Footer */

/* Estilos para el footer */
.page-footercue {
	background-color: #2d3e50;
	color: #fff;
	padding: 60px 0;
  }
  
  .footer-first-sectioncue {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
  }
  
  .containercue {
	max-width: 1100px;
	margin: 0 auto;
	padding: 0 15px;
  }
  
  .box-wrapcue {
	flex-basis: 45%;
	padding: 20px;
	margin-bottom: 30px;
	border-radius: 5px;
	background-color: #34495e;
  }
  
  .box-wrapcue h1 {
	font-size: 24px;
	margin-bottom: 20px;
  }
  
  .box-wrapcue p {
	font-size: 16px;
	line-height: 1.5;
  }
  
  .box-wrapcue h4 {
	font-size: 18px;
	margin-bottom: 10px;
  }
  
  .box-wrapcue a {
	color: #fff;
	text-decoration: none;
  }
  
  .box-wrapcue i {
	margin-right: 10px;
  }
  
  @media only screen and (max-width: 768px) {
	.footer-first-sectioncue {
	  flex-direction: column;
	  align-items: center;
	}
  
	.box-wrapcue {
	  flex-basis: 100%;
	}
  
	.box-wrapcue:not(:last-child) {
	  margin-bottom: 20px;
	}
  }

  /* Estilos Home */
  /* styles.css */

  /* Estilos para paquetes populares */
.page-heading {
    background-color: #DDE7F2;
    padding: 2rem 0;
    text-align: center;
  }
  
  .page-heading h2 {
    color: #27496D;
    font-size: 2rem;
  }
  
  .learn-courses {
    background-color: #F5F8FA;
    padding: 2rem 0;
  }
  
  .courses {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  @media only screen and (max-width: 768px) {
    .courses {
      justify-content: flex-start;
    }
  }

/* Galery Styles */
/* Galería */
.page-heading h2 {
    color: #333;
    font-size: 2rem;
    font-weight: bold;
    text-align: center;
    margin: 1rem 0;
  }
  
  .gallery-images-section {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: 1rem auto;
  }
  
  .gallery-img-wrap {
    margin: 1rem;
   /*width: calc(20% - 2rem);  5 images per row */
  }
  
  .gallery-img-wrap a {
    display: block;
    width: 100%;
    height: 100%;
    padding: 0.5rem;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
    transition: all 0.3s ease-in-out;
  }
  
  .gallery-img-wrap a:hover {
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.5);
  }
  
  .gallery-img-wrap img {
    display: block;
    width: 100%;
    height: auto;
    border-radius: 5px;
  }
  
  @media screen and (min-width: 768px) {
    .gallery-images-section {
      max-width: 800px;
      margin: 2rem auto;
    }
    
    .gallery-img-wrap {
      width: calc(33.33% - 2rem); /* 3 images per row */
    }
  }
  
  @media screen and (min-width: 992px) {
    .gallery-images-section {
      max-width: 1000px;
    }
  
    .gallery-img-wrap {
      width: calc(25% - 2rem); /* 4 images per row */
    }
  }
  
  @media screen and (min-width: 1200px) {
    .gallery-images-section {
      max-width: 1200px;
    }
  
    .gallery-img-wrap {
      width: calc(20% - 2rem); /* 5 images per row */
    }
  }
  
  .page-heading h2 {
    color: #333;
    font-size: 2rem;
    font-weight: bold;
    text-align: center;
    margin: 1rem 0;
  }
  
  /* Eventos */
  .events {
    margin:2rem auto;
    display: flex;
    flex-wrap: wrap;
  }
  
  .event-wrap {
    width: 100%;
    display: flex;
    flex-wrap: nowrap;
    margin-bottom: 40px;
  }
  
  .img-wrap {
    width: 100%;
    margin-right: 30px;
    margin-bottom: 20px;
  }
  
  .img-wrap img {
    width: 100%;
    height: auto;
  }
  
  .details {
    width: 100%;
  }
  
  .details h3 {
    color: #1a2942;
    font-size: 24px;
    font-weight: 700;
    margin: 0;
  }
  
  .details p {
    color: #637381;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
    margin-bottom: 20px;
  }
  
  .details h5 {
    color: #637381;
    font-size: 16px;
    font-weight: 400;
    margin-bottom: 10px;
  }
  
  .details h5 i {
    margin-right: 10px;
  }
  
  @media screen and (min-width: 992px) {
    .event-wrap {
      width: 32%;
    }
  }

  /* pack slides */
  .slick-slide img {
    margin: auto;
  }

  .slick-slide img {
    width: 100%;
    height: auto;
    max-width: 400px; /* Este es un ejemplo, ajusta el tamaño a tus necesidades */
    max-height: 400px; /* Este es un ejemplo, ajusta el tamaño a tus necesidades */
  }

  .pack-image {
    width: 100%;
    height: auto;
    max-width: 400px; /* Este es un ejemplo, ajusta el tamaño a tus necesidades */
    max-height: 400px; /* Este es un ejemplo, ajusta el tamaño a tus necesidades */
  }

  /*Estilos Boton Regresar*/
  /* Estilos CSS */
  button {
    background-color: transparent;
    border: none;
    color: inherit;
    font-size: inherit;
    cursor: pointer;
  }
  
  button {
    background-color: #b3d4fc;
    border: 2px solid #3b82f6;
    color: inherit;
    font-size: inherit;
    cursor: pointer;
    padding: 8px 16px;
    border-radius: 4px;
  }
  
  button:hover {
    background-color: #a5c1f7;
  }
  
  

